import React, { useState } from "react";
import Header from "../components/header"
import { Link } from "gatsby"

const IndexPage = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  return (
    <div className="wrapper">
     <Header onMenuToggle={(isOpen) => setIsMenuOpen(isOpen)} />
      {/* <h1>Witaj na mojej stronie Gatsby!</h1> */}
      <main className={isMenuOpen ? "blurred" : ""}>
        <img src="/icons/6.jpg" alt="Background" />
        <div class="overlay">
          <h1>
            Witamy na stronie<br></br> Hurtowni Materiałów Budowlanych
          </h1>
          <br></br>
          <div className="shopbtnmain">
            <p>klinkier to nasza specjalność...</p>
            <Link href=''>Sklep online</Link>
          </div>
        </div>
      </main>
      <section className={isMenuOpen ? "blurred" : ""}>JOŁ</section>
      <section className={isMenuOpen ? "blurred" : ""}>JOŁ</section>
    </div>
  )
}

export default IndexPage
